import {Button, Link, Paper, TextField} from "@material-ui/core";
import {XGrid} from "@material-ui/x-grid";
import React, {useState} from "react";
import {useSelector} from "react-redux";
import EditForm from "./EditForm";
import useOrders from "./hooks/useOrders";
import StatusPopper from "./StatusPopper";
import ToolTip from "./ToolTip";
import "./Table.css";
import useExportOrders from "./hooks/useExportOrders";

function strcmp(a, b) {
  return a.localeCompare(b);
}

const formatDate = (date) =>
  new Intl.DateTimeFormat("en-GB", {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
    hour: "2-digit",
    minute: "2-digit",
    second: "2-digit",
    hour12: false,
  }).format(date);

export default function Table({tableID}) {
  const sortState = useSelector((state) => state.sortSelections);
  const authState = useSelector((state) => state.auth);
  const ordersQuery = useOrders(tableID, sortState[sortState.active]);
  const exportQuery = useExportOrders();
  const [q, setQ] = useState("");

  const columns = [
    {
      field: "orderId",
      type: "number",
      headerName: "",
      flex: 0.8,
      renderCell: (params) => {
        if (
          authState.roles.includes("ROLE_ADMIN") ||
          authState.roles.includes("ROLE_ORDERS_EDITOR") ||
          params.row.employee.employeeID === authState.loggedUser.employeeID
        ) {
          return <EditForm order={params.row} />;
        } else {
          return (
            <ToolTip title={params.row.employee.name}>
              <Link
                style={{
                  color: "rgba(0, 60, 170, 0.95)",
                  textDecoration: "none",
                }}
              >
                <b>{params.row.orderId}</b>
              </Link>
            </ToolTip>
          );
        }
      },
    },
    {
      field: "billNo",
      type: "number",
      headerName: "",
      flex: 0.6,
    },
    {
      field: "customer",
      headerName: "",
      flex: 2.2,
      sortComparator: (c1, c2) => strcmp(c1.customerName, c2.customerName),
      valueFormatter: (param) => param.value.customerName,
      renderCell: (params) =>
        params.value.info ? (
          <ToolTip title={params.value.info}>
            <b>{params.value.customerName}</b>
          </ToolTip>
        ) : (
          <b>{params.value.customerName}</b>
        ),
    },
    {
      field: "date",
      type: "dateTime",
      headerName: "",
      flex: 1.2,
      valueFormatter: (param) => formatDate(param.value),
    },
    {
      field: "width",
      type: "number",
      headerName: "",
      valueFormatter: (row) => row.value,
      flex: 1.09,
    },
    {
      field: "height",
      type: "number",
      headerName: "",
      valueFormatter: (row) => row.value,
      flex: 1,
    },
    {
      field: "amount",
      type: "number",
      headerName: "-",
      valueFormatter: (row) => row.value,
      flex: 0.85,
    },
    {
      field: "m2",
      type: "number",
      headerName: "2",
      valueFormatter: (row) => Number.parseFloat(row.value).toFixed(3),
      flex: 0.9,
    },
    {
      field: "price",
      type: "number",
      headerName: "",
      valueFormatter: (row) => row.value,
      flex: 0.8,
    },
    {
      field: "fees",
      type: "number",
      headerName: "",
      valueFormatter: (row) => row.value,
      flex: 1,
    },
    {
      field: "finalSum",
      type: "number",
      headerName: "",
      flex: 0.9,
      renderCell: (params) => <b> {params.value} </b>,
    },
    {
      field: "paid",
      headerName: "",
      sortable: false,
      flex: 1.1,
      cellClassName: "centered",
      renderCell: (params) => {
        if (params.row.paySum === params.row.finalSum) {
          return (
            <ToolTip title={formatDate(new Date(params.row.payDate))} arrow>
              <img src="img/completed.svg" width="19" height="19" alt="paid" />
            </ToolTip>
          );
        } else if (params.row.paySum > 0)
          return (
            <ToolTip
              title={
                params.row.paySum +
                " , " +
                formatDate(new Date(params.row.payDate))
              }
              arrow
            >
              <img
                src="img/partly_paid.svg"
                width="19"
                height="19"
                alt="partly__paid"
              />
            </ToolTip>
          );
      },
    },
    {
      field: "status",
      headerName: "",
      flex: 1.1,
      cellClassName: "centered",
      sortComparator: (c1, c2) => c1.id - c2.id,
      renderCell: (params) => (
        <StatusPopper orderId={params.row.orderId} status={params.row.status.id} />
      ),
    },
    {
      field: "orderType",
      type: "string",
      headerName: "",
      flex: 1.8,
      sortComparator: (c1, c2) => strcmp(c1.typeName, c2.typeName),
      valueFormatter: (param) => param.value.typeName,
    },
    {
      field: "orderKind",
      headerName: "",
      flex: 1.8,
      sortComparator: (c1, c2) => strcmp(c1.kindName, c2.kindName),
      valueFormatter: (param) => param.value.kindName,
    },
    {
      field: "comment",
      headerName: " ",
      flex: 6,
      sortComparator: (c1, c2) => strcmp(c1, c2),
      renderCell: (params) => (
        <ToolTip title={params.value} placement="left-start">
          {params.value}
        </ToolTip>
      ),
    },
  ];

  const filter = (rows) => {
    return rows.filter(
      (row) =>
        row.orderId?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.billNo?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.customer.customerName.toString().toLowerCase().indexOf(q.toLowerCase()) >
          -1 ||
        formatDate(row.date)?.toString().toLowerCase().indexOf(q.toLowerCase()) >
          -1 ||
        row.fees?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.finalSum?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.amount?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.price?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.height?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.width?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.m2?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1 ||
        row.orderKind.kindName?.toString().toLowerCase().indexOf(q.toLowerCase()) >
          -1 ||
        row.orderType.typeName?.toString().toLowerCase().indexOf(q.toLowerCase()) >
          -1 ||
        row.comment?.toString().toLowerCase().indexOf(q.toLowerCase()) > -1
    );
  };

  const calculateDebt = (customer) => {
    const ordersDiff = ordersQuery.data.reduce(
      (a, b) => (a += b.finalSum - b.paySum),
      0
    );
    return ordersDiff - customer.money;
  };

  return (
    <>
      <TextField
        variant="outlined"
        size="small"
        placeholder=" "
        onChange={(e) => setQ(e.target.value)}
        InputProps={{
          endAdornment: authState.roles.includes("ROLE_ORDERS_EXPORTER") && (
            <Button
              className="export-btn"
              variant="outlined"
              onClick={() => exportQuery.mutate(filter(ordersQuery.data))}
            >
              
            </Button>
          ),
        }}
      />

      <Paper elevation={4} className="x-grid">
        {sortState[sortState.active].selectedCustomer && (
          <div className="action__bar__stat">
            <label>
                :{" "}
              <b>{calculateDebt(sortState[sortState.active].selectedCustomer)}</b>{" "}
              
            </label>
          </div>
        )}
        <div className="frame">
          <div className="grid-holder">
            <XGrid
              rows={filter(ordersQuery.data)}
              columns={columns}
              sortModel={[{field: "orderId", sort: "desc"}]}
              loading={ordersQuery.isLoading}
              rowHeight={35}
              headerHeight={35}
              pagination={true}
              disableSelectionOnClick={true}
            />
          </div>
        </div>
      </Paper>
    </>
  );
}
